#list-wrapper{
  position:absolute;
  bottom: 0;
  width:100%;
  z-index: map-get($element-layers, list);
  
  
  @include orientation(portrait){
    @include gradien-stops(vertical, rgba($general-dark_blue,0), rgba($general-dark_blue,1), 15% );
    width: 100%;
    height: map-get($globe-list, list);
    #list{
      width: 80%;
      left: 10%;
      height: 90%;

      ul{
        height: 72%;
        .mobile &{
          height: 77%;
        }
        
      }
    };
    @include respond-to('medium') {
      #list{
        width: 50%;
        left: 25%;
        height: 90%;
  
        ul{
          height: 72%;
          height: calc(100% - 5em);
        }
      };
    }
    &.active{
      height: 45%;
      @include respond-to('medium') {
        height: map-get($globe-list, list);
        #list{
          padding-top: 2em;
          height: 80%;
        }
      }
      #list{
        // padding-top: 2em;
        height: 88%;
      }
    }
    &.collapsed{
      height: 7em;
      .mobile &{
        height: 3.5em;
      }
      ul{
        display:none;
      }
      @include respond-to('medium') {
        height: map-get($globe-list, list);
        ul{
          display:block;
        }
        #list{
          padding-top: 2em;
          height: 80%;
        }
      }
    }
  }

  @include orientation(landscape){
    width: map-get($globe-list, list);
    top: 0;
    right:0;
    @include gradien-stops(horizontal,rgba($general-dark_blue,0), rgba($general-dark_blue,1), 15% );
    #list{
      width: calc(80%);
      left: 10%;
      top:10em;
      height:85%;
      ul{
        height: 70%;
        height: calc(100% - 13em);
      }
    };
    
  }
  
  h6, button{
    margin: 0;
  }
}

#list{
  position:absolute;
  margin:0 auto;
  top: 0;
  width: 50%;
  left: 25%;
  color: $grays-level_1;
  overflow-y: hidden;
  overflow-x: hidden;
  .message{
    background: $grays-level_2;
    color:$general-dark_blue;
    padding: 1em 2em;
    width:fit-content;
    float: right;
    // position: absolute;
    text-align: right;
    font-family: "GothamBook";
    font-size: 10px;

    @include respond-to('small') {
      font-size: 12px;
    }
    @include respond-to('medium') {
      font-size: 14px;
    }
    @include respond-to('large') {
      font-size: 16px;
    }
  }
  ul{
    
    // position: relative;
    overflow-y: auto;
    text-align: right;
    padding:0;
    margin: 0;
    width:100%;
    scrollbar-color: $grays-level_2 transparent;
    &::-webkit-scrollbar {
        width: 7px;
        background-color:var(--general-dark_blue,$general-dark_blue);
    }
    &::-webkit-scrollbar-corner, &::-webkit-resizer,
    &::-webkit-scrollbar-button, &::-webkit-scrollbar-track,
    &::-webkit-scrollbar:horizontal {
      display: none;
    }
    &::-webkit-scrollbar-thumb {
      background-color: var(--grays-level_2,$grays-level_2);
    }
    &::-webkit-scrollbar-track-piece:vertical{
        width:2px !important;
        background: var(--grays-level_2, $grays-level_2);
        border-left:3px solid var(--general-dark_blue,$general-dark_blue);
        border-right:3px solid var(--general-dark_blue,$general-dark_blue);
    }

    li{
          margin: .7em 4.5em;

          padding: 0.21em 0;
          margin: 0;
          // display:block;
          width:95%;
          position:relative;
          list-style:none;
          // height:3.5em;
          button{
            @extend %button;
            position: relative;
            right: 3em;
            margin: 0;
            padding: 0;
            width:80%;
            color:inherit;
            font-size: inherit;
            margin: 0.5em 0;

          }
          font-size: 12px;

          @include respond-to('small') {
            font-size: 14px;
          }
          @include respond-to('medium') {
            font-size: 16px;
          }
          @include respond-to('large') {
            font-size: 18px;
          }
          h6{//industry vertical
           margin: 0;
           padding: 0;
           margin-bottom: .2em;
           letter-spacing: 0.2em;
           text-align: right;
           font-family: "Futura-Medium";
           font-size: 0.81em;

          //  @include respond-to('small') {
          //    font-size: 0.81em;
          //  }
          //  @include respond-to('medium') {
          //    font-size: 0.81em;
          //  }
          //  @include respond-to('large') {
          //    font-size: 0.81em;
          //  }
          }
          h4{//copmany name
            margin: 0;
            padding: 0;
            line-height: 1.2em;
            text-align: right;
            font-family:"Futura-Bold";
            font-size: inherit;
            letter-spacing: 0.2em;
        
          }
          svg{// arrow
           width: 2em;
           position: absolute;
           top: 50%;
           right: 0;
           transform: translate(125%, -50%);
           [class*="circle"] {
            stroke-width:4;
            stroke-miterlimit:10;
            fill: none;
           }
           .cls-3 {
            //  fill: var(--vertical-color);
           }
          }


    }
  }
 
  .collapse-btn{
    @extend %button;
    position: relative;
    font-size: 1em;
    background:transparent;
    width:1.5em;
    height: 1.5em;
    padding:0;
    top: -0.3em;
    margin:0 0 0 .8em;
    border-right: 3px solid $grays-level_3;
    border-bottom: 3px solid $grays-level_3;
    // transform: rotate(-135deg);
    
    @include respond-to('small') {
      // display: none;
    }
    @include respond-to('medium') {
      display: none;
    }
    @include respond-to('large') {
      display: none;
    }
    span{// hit area
      position: absolute;
      width: 4em;
      height: 4em;
      // background: rgba(red, 0.4);
      transform: translate(-50%, -50%);
    }
  }

}
.active{
  .collapse-btn{
    transform: scale(0.75, 1) rotate(45deg);
  }
}
.collapsed{
  .collapse-btn{
    top: 0.5em !important;
    transform: scale(0.75, 1) rotate(-135deg);
  }
}