@import '../../styles/components.scss';
@import "../../styles/mixins.scss";
@import "../../styles/colors.scss";
@import "../../styles/typography.scss";

#globe{
  position:absolute;
  margin:0;
  top: 0;
  left:0;
  width: 55%;
  height: 100%;
  z-index: map-get($element-layers, globe);
  @include orientation(portrait){
    width: 100%;
    height: 100%;
  }
  @include respond-to('small', portrait) {
    width: 100%;
    height: 100%;
  }
  @include respond-to('medium', portrait) {
    width: 100%;
    height: map-get($globe-list, globe);
  }
  @include respond-to('large', portrait) {
    width: 100%;
    height: map-get($globe-list, globe);
  }
  @include orientation(landscape){
    width: map-get($globe-list, globe);
  }
  @include respond-to('large', portrait) {
    // width:45%;
    // width: map-get($globe-list, globe);
  }
}
