@import '../../styles/components.scss';
@import "../../styles/mixins.scss";
@import "../../styles/colors.scss";
@import "../../styles/typography.scss";

#instructions-wrapper{
  position:absolute;
  width:100%;
  height:100%;
  background: rgba($general-dark_blue, 0.95);
  z-index: map-get($element-layers, instructions);
}

#instructions{
  position:relative;
  margin:0 auto;
  width: 70%;

  top:50%;
  transform: translateY(-50%);
  text-align:center;
  color: $grays-level_1;
  letter-spacing: 0.25em;

  h4{//title
    font-family:"Futura-Bold";
    font-size: 14px;
    
    @include respond-to('small') {
      font-size: 18px;
    }
    @include respond-to('medium') {
      font-size: 28px;
    }
    @include respond-to('large') {
      font-size: 34px;
    }
  }

  p{//copy
    font-family:"GothamBook";
    line-height: 1.5em;
    max-width: 800px;
    margin: 0 auto;

    font-size: 12px;
    @include respond-to('small') {
      font-size: 14px;
    }
    @include respond-to('medium') {
      font-size: 16px;
    }
    @include respond-to('large') {
      font-size: 18px;
    }
  }
  .icons{
    margin: 2em 0 0 0;
    position: relative;
    button{
      @extend %button;
      color:$grays-level_1;
      display: block;
      margin: 2em auto;
      // position: absol;
      border: 3px solid $grays-level_1;
      padding: 1em 2em;
      font-family:"Futura-Bold";
      font-size: 14px;
      @include box-shadow(0px 3px 29px 5px rgba(0,0,0,.5));
      @include transition(all 300ms ease-in);
      &:hover{
        @include box-shadow(0px 3px 29px 5px rgba($grays-level_3,.5));
      }
      @include respond-to('small') {
        font-size: 18px;
      }
      @include respond-to('medium') {
        font-size: 28px;
      }
      @include respond-to('large') {
        font-size: 34px;
      } 
    }
  }
}
