@import "./typography.scss";
@import "./colors.scss";
@import './mixins.scss';
@import './components.scss';

$app-padding: 0px;
$primary-color: #333;


body{
  margin:0;
}
#app{
  position: relative;
  left:0;
  box-sizing: border-box;
  padding: $app-padding;
  background: red;
  width: 100%;
  height: 900px;
  font-family:"GothamBook", sans-serif;
  overflow: hidden;
}

.globe-interactive {
  position: absolute;
  box-sizing: border-box;
  background: $general-dark_blue;

  width: calc( 100% - #{$app-padding*2} );
  height: calc( 100% - #{$app-padding*2} );

  .show-coomponent{
    opacity: 1;
    @include transition(opacity 1500ms ease-in-out);
  }
  .hide-component{
    opacity:0;
    pointer-events: none;
    // @include transition(opacity 800ms ease-out);
    @include transition(opacity 1.5s ease-in-out);
  }
}


#globe-interactive{
}
.fullscreen-btn{
  @extend %button;
  position: absolute;
  top: 0.5em;
  right: 0;
  z-index: 1000;
  font-size: inherit;
  width: 3em;
}
.fullscreen{
  position: fixed !important;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
}