// write some mixins
$breakpoints: (
  'small':  767px,
  'medium': 992px,
  'large':  1200px
) !default;


/// @require $breakpoints
@mixin respond-to($breakpoint, $o:null ) {
  // If the key exists in the map
  @if map-has-key($breakpoints, $breakpoint) {
		// Prints a media query based on the value
		@if $o {
			@media (min-width: map-get($breakpoints, $breakpoint)) and (orientation: $o){
				@content;
			}
		}
		@else{
			@media (min-width: map-get($breakpoints, $breakpoint)) {
				@content;
			}
		}
  }
 
  // If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints)}.";
  }
}

@mixin orientation($o) {  
	// Prints a media query based on the value
	@media screen and (orientation: $o) {
		@content;
	}
}

@mixin text-shadow ($string: 0 1px 3px rgba(0, 0, 0, 0.25)) {
	text-shadow: $string;
}
@mixin text-outline($text-outline: 0.25px, $text-ouline-color: #fff){
	text-shadow: -$text-outline  -$text-outline  0 $text-ouline-color, $text-outline  -$text-outline  0 $text-ouline-color, -$text-outline  $text-outline  0 $text-ouline-color, $text-outline  $text-outline  0 $text-ouline-color;
}
@mixin box-shadow ($string) {
	-webkit-box-shadow: $string;
	-moz-box-shadow:    $string;
	box-shadow:         $string;
}
@mixin drop-shadow ($x: 0, $y: 1px, $blur: 2px, $spread: 0, $alpha: 0.25) {
	-webkit-box-shadow:	$x $y $blur $spread rgba(0, 0, 0, $alpha);
	-moz-box-shadow:	$x $y $blur $spread rgba(0, 0, 0, $alpha);
	box-shadow:		$x $y $blur $spread rgba(0, 0, 0, $alpha);
}
@mixin inner-shadow ($x: 0, $y: 1px, $blur: 2px, $spread: 0, $alpha: 0.25) {
	-webkit-box-shadow: inset $x $y $blur $spread rgba(0, 0, 0, $alpha);
	-moz-box-shadow:    inset $x $y $blur $spread rgba(0, 0, 0, $alpha);
	box-shadow:         inset $x $y $blur $spread rgba(0, 0, 0, $alpha);
}

@mixin box-sizing ($type: border-box) {
	-webkit-box-sizing: $type;
	-moz-box-sizing:    $type;
	box-sizing:         $type;
}

@mixin border-radius ($radius: 5px) {
	-webkit-border-radius: $radius;
	-moz-border-radius:    $radius;
	border-radius:         $radius;

	// -moz-background-clip:    padding;
	// -webkit-background-clip: padding-box;
	// background-clip:         padding-box;
}
@mixin border-radiuses ($topright: 0, $bottomright: 0, $bottomleft: 0, $topleft: 0) {
	-webkit-border-top-right-radius:    $topright;
	-webkit-border-bottom-right-radius: $bottomright;
	-webkit-border-bottom-left-radius:  $bottomleft;
	-webkit-border-top-left-radius:     $topleft;

	-moz-border-radius-topright:        $topright;
	-moz-border-radius-bottomright:     $bottomright;
	-moz-border-radius-bottomleft:      $bottomleft;
	-moz-border-radius-topleft:         $topleft;

	border-top-right-radius:            $topright;
	border-bottom-right-radius:         $bottomright;
	border-bottom-left-radius:          $bottomleft;
	border-top-left-radius:             $topleft;

	-moz-background-clip:    padding;
	-webkit-background-clip: padding-box;
	background-clip:         padding-box;
}

@mixin opacity ($opacity: 0.5) {
	-webkit-opacity: 	$opacity;
	-moz-opacity: 		$opacity;
	opacity: 		$opacity;
}

@mixin gradient ($startColor: #eee, $endColor: white, ) {
	background-color: $startColor;
	background: -webkit-gradient(linear, left top, left bottom, from($startColor), to($endColor));
	background: -webkit-linear-gradient(top, $startColor, $endColor);
	background: -moz-linear-gradient(top, $startColor, $endColor);
	background: -ms-linear-gradient(top, $startColor, $endColor);
	background: -o-linear-gradient(top, $startColor, $endColor);
}


@mixin gradien-stops($dir, $startColor: #eee, $endColor: white, $pos:85% ){
	@if $dir == vertical {
		/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#1e5799+0,7db9e8+98&1+83,0+100 */
		background: -moz-linear-gradient(top,  $startColor 0%,    $endColor $pos ); /* FF3.6-15 */
		background: -webkit-linear-gradient(top,  $startColor 0%, $endColor $pos ); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom,  $startColor 0%, $endColor $pos ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$startColor, endColorstr=$endColor,GradientType=0 ); /* IE6-9 */
	}
	@else{
		/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#1e5799+0,7db9e8+98&1+83,0+100 */
		background: -moz-linear-gradient(left,  $startColor 0%,    $endColor $pos ); /* FF3.6-15 */
		background: -webkit-linear-gradient(left,  $startColor 0%, $endColor $pos ); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right,  $startColor 0%, $endColor $pos ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$startColor, endColorstr=$endColor ,GradientType=1 ); /* IE6-9 */

	}
}
@mixin horizontal-gradient ($startColor: #eee, $endColor: white) {
 	background-color: $startColor;
	background-image: -webkit-gradient(linear, left top, right top, from($startColor), to($endColor));
	background-image: -webkit-linear-gradient(left, $startColor, $endColor);
	background-image: -moz-linear-gradient(left, $startColor, $endColor);
	background-image: -ms-linear-gradient(left, $startColor, $endColor);
	background-image: -o-linear-gradient(left, $startColor, $endColor);
}

