@mixin animation ($anim) {
	-webkit-animation: $anim;
	-moz-animation:    $anim;
	-ms-animation:     $anim;
  animation:     $anim;
}
@mixin two-animations ($firstAnim, $secondAnim) {
	-webkit-animation: $firstAnim, $secondAnim;
	-moz-animation:    $firstAnim, $secondAnim;
	-ms-animation:     $firstAnim, $secondAnim;
  animation:     $firstAnim, $secondAnim;
}

@mixin transition ($transition) {
	-webkit-transition: $transition;
	-moz-transition:    $transition;
	-ms-transition:     $transition;
	-o-transition:      $transition;
  transition:      $transition;
}
@mixin transition2 ($transition, $transition2) {
	-webkit-transition: $transition, $transition2;
	-moz-transition:    $transition, $transition2;
	-ms-transition:     $transition, $transition2;
	-o-transition:      $transition, $transition2;
  transition:      $transition, $transition2;
}
@mixin transition3 ($transition, $transition2, $transition3) {
	-webkit-transition: $transition, $transition2, $transition3;
	-moz-transition:    $transition, $transition2, $transition3;
	-ms-transition:     $transition, $transition2, $transition3;
	-o-transition:      $transition, $transition2, $transition3;
  transition:      $transition, $transition2, $transition3;
}
@mixin transition4 ($transition, $transition2, $transition3,$transition4) {
	-webkit-transition: $transition, $transition2, $transition3,$transition4;
	-moz-transition:    $transition, $transition2, $transition3,$transition4;
	-ms-transition:     $transition, $transition2, $transition3,$transition4;
	-o-transition:      $transition, $transition2, $transition3,$transition4;
  transition:      $transition, $transition2, $transition3,$transition4;
}
@mixin transition5 ($transition, $transition2, $transition3, $transition4, $transition5) {
	-webkit-transition: $transition, $transition2, $transition3, $transition4, $transition5;
	-moz-transition:    $transition, $transition2, $transition3, $transition4, $transition5;
	-ms-transition:     $transition, $transition2, $transition3, $transition4, $transition5;
	-o-transition:      $transition, $transition2, $transition3, $transition4, $transition5;
  transition:      $transition, $transition2, $transition3, $transition4, $transition5;
}
@mixin transform($string){
	-webkit-transform: $string;
	-moz-transform: 	 $string;
	-ms-transform: 		 $string;
	-o-transform: 		 $string;
  transform: 		 $string;
}
@mixin scale ($factor) {
	-webkit-transform: scale($factor);
	-moz-transform: 	 scale($factor);
	-ms-transform: 		 scale($factor);
	-o-transform: 		 scale($factor);
  transform: 		 scale($factor);
}
@mixin rotate ($deg) {
	-webkit-transform: rotate($deg);
	-moz-transform: 	 rotate($deg);
	-ms-transform: 		 rotate($deg);
	-o-transform: 		 rotate($deg);
	transform: 		 rotate($deg);
}
@mixin skew ($deg, $deg2) {
	-webkit-transform:       skew($deg, $deg2);
	-moz-transform: 	 skew($deg, $deg2);
	-ms-transform: 		 skew($deg, $deg2);
	-o-transform: 		 skew($deg, $deg2);
	transform: 		 skew($deg, $deg2);
}
@mixin translate ($x, $y:0) {
	-webkit-transform:       translate($x, $y);
	-moz-transform: 	 translate($x, $y);
	-ms-transform: 		 translate($x, $y);
	-o-transform: 		 translate($x, $y);
	transform: 		 translate($x, $y);
}
@mixin translate3d ($x, $y: 0, $z: 0) {
	-webkit-transform:       translate3d($x, $y, $z);
	-moz-transform: 	 translate3d($x, $y, $z);
	-ms-transform: 		 translate3d($x, $y, $z);
	-o-transform: 		 translate3d($x, $y, $z);
	transform: 		 translate3d($x, $y, $z);
}
@mixin perspective ($value: 1000) {
	-webkit-perspective: 	$value;
	-moz-perspective: 	$value;
	-ms-perspective: 	$value;
	perspective: 		$value;
}
@mixin transform-origin ($x:center, $y:center) {
	-webkit-transform-origin: $x $y;
	-moz-transform-origin:    $x $y;
	-ms-transform-origin:     $x $y;
	-o-transform-origin:      $x $y;
  transform-origin:      $x $y;
}
