@import '../../styles/components.scss';
@import "../../styles/mixins.scss";
@import "../../styles/colors.scss";
@import "../../styles/typography.scss";

#filter{
  position:absolute;
  box-sizing:border-box;
  margin:0;
  width: 95%;
  left: 2.5%;
  background: rgba($general-dark_blue, 0.4);
  z-index: map-get($element-layers, filter);
  @include respond-to('small') {
    width: 50%;
  left: 25%;
  }
  .expand-btn{
    @include transition(all  300ms ease-in);
    color:$grays-level_1;
    text-align: center;
    text-transform: uppercase;
    @include respond-to('small') {
      height: 0;
      opacity: 0; 
    }
    @include respond-to('medium') {
      height: 0;
      opacity: 0;
    }
    @include respond-to('large') {
      height: 0;
      opacity: 0;
    }
    svg{
      fill:$grays-level_1;
      width: 0.8em;
      margin-right: 0.8em; 
      transform: translate(0, 10%);
      @include transition(all 300ms ease-in);
    }
    &.active{
      color:$grays-level_3;
      svg{
        fill:$grays-level_3;
      }
    }
  }
  .picker-container{
    height: 0 !important;
    @include transition(height  200ms ease-in);
    .picker-highlight{
      opacity: 0;
      @include transition(opacity  200ms ease-in);
    }
    &.mobile-open{
      height: 150px !important;
      .picker-highlight{
        opacity: 1;
      }
    }
    @include respond-to('small') {
      height: 150px !important;
      .picker-highlight{
        opacity: 1;
      }
    }
    @include respond-to('medium') {
      height: 150px !important;
      .picker-highlight{
        opacity: 1;
      }
    }
    @include respond-to('large') {
      height: 150px !important;
      .picker-highlight{
        opacity: 1;
      }
    }

  }

  .picker-item {
    font-family:"Futura-Medium";
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 10px;
    @include respond-to('small') {
      font-size: 10px;
    }
    @include respond-to('medium') {
      font-size: 14px;
    }
    @include respond-to('large') {
      font-size: 14px;
    }
    
    @include transition(color 300ms ease-in);
    color: darken(#999999,20%);
    &[class*="horizontal"]{color: darken($verticals-horizontal,10%); }
    &[class*="nonprofit"]{color: darken($verticals-nonprofit,10%); }
    &[class*="education"]{color: darken($verticals-education,10%); }
    &[class*="energy"]{color: darken($verticals-energy,10%); }
    &[class*="government"]{color: darken($verticals-government,10%); }
    &[class*="agriculture"]{color: darken($verticals-agriculture,10%); }
    &[class*="healthcare"]{color: darken($verticals-healthcare,10%); }
    &[class*="insurance"]{color: darken($verticals-insurance,10%); }
    &[class*="legal"]{color: darken($verticals-legal,10%); }
    &[class*="hr-n-recruiting"]{color: darken($verticals-hr-n-recruiting,10%); }
    &[class*="managed-services"]{color: darken($verticals-managed-services,10%); }
    &[class*="financial-services"]{color: darken($verticals-financial-services,10%); }
    &[class*="sales-n-marketing"]{color: darken($verticals-sales-n-marketing,10%); }
    &[class*="retail"]{color: darken($verticals-retail,10%); }
    &[class*="hospitality"]{color: darken($verticals-hospitality,10%); }
    &[class*="real-estate"]{color: darken($verticals-real-estate,10%); }
    &[class*="sports-media-n-entertainment"]{color: darken($verticals-sports-media-n-entertainment,10%); }
    &[class*="security"]{color: darken($verticals-security,10%); }
    &[class*="telecom"]{color: darken($verticals-telecom,10%); }
    &[class*="automotive"]{color: darken($verticals-automotive,10%); }
    &[class*="transportation"]{color: darken($verticals-transportation,10%); }
    &[class*="vista"]{color: darken($verticals-vista,10%); }
    &.picker-item-selected {
      color: #999999;
      &[class*="horizontal"]{color: $verticals-horizontal;}
      &[class*="nonprofit"]{color: $verticals-nonprofit;}
      &[class*="education"]{color: $verticals-education;}
      &[class*="energy"]{color: $verticals-energy;}
      &[class*="government"]{color: $verticals-government;}
      &[class*="agriculture"]{color: $verticals-agriculture;}
      &[class*="healthcare"]{color: $verticals-healthcare;}
      &[class*="insurance"]{color: $verticals-insurance;}
      &[class*="legal"]{color: $verticals-legal;}
      &[class*="hr-n-recruiting"]{color: $verticals-hr-n-recruiting;}
      &[class*="managed-services"]{color: $verticals-managed-services;}
      &[class*="financial-services"]{color: $verticals-financial-services;}
      &[class*="sales-n-marketing"]{color: $verticals-sales-n-marketing;}
      &[class*="retail"]{color: $verticals-retail;}
      &[class*="hospitality"]{color: $verticals-hospitality;}
      &[class*="real-estate"]{color: $verticals-real-estate;}
      &[class*="sports-media-n-entertainment"]{color: $verticals-sports-media-n-entertainment;}
      &[class*="security"]{color: $verticals-security;}
      &[class*="telecom"]{color: $verticals-telecom;}
      &[class*="automotive"]{color: $verticals-automotive;}
      &[class*="transportation"]{color: $verticals-transportation;}
      &[class*="vista"]{color: $verticals-vista;}
    }
  }
}
