@import '../../styles/components.scss';
@import "../../styles/mixins.scss";
@import "../../styles/colors.scss";
@import "../../styles/typography.scss";

$node-dia-hq: 10em;
$node-dia-years-of-investment: 10em;
$node-dia-other_locations: 8em;
$node-dia-year: 6em;
$node-dia-add_on_companies: 8em;
$node-dia-logo: 5em;
$shadow-scale: 1.1;

$time-out-fade: (
  appear: 500ms,
  enter: 500ms,
  exit: 500ms,
);
%dotted{
  stroke: $grays-level_1;
  stroke-dasharray: 2px 6px;
  stroke-width: 2px;
}
.background{
  @extend .full-frame !optional;
  height: 100%;
  width: 100%;
  background: $general-dark_blue;
  z-index:5;
}
.company-wrapper{
  z-index: map-get($element-layers, company);
  
}
#company{
  @extend .full-frame !optional;
  height: 100%;
  position: absolute;
  background: $general-dark_blue;
  width: 100%;
  color: $grays-level_1;
  z-index: map-get($element-layers, company);
  .content-wrapper{
    position: absolute;
    width:100%;
    height: 100%;
  }
  font-size: 10px;
  @include respond-to('small') {
    font-size: 12px;
  }
  @include respond-to('medium') {
    font-size: 14px;
  }
  @include respond-to('large') {
    font-size: 16px;
  }
  // @include transition(transform 300ms ease-in);
  &.AddOnCompanies-active{
    .content-wrapper{
      .mobile &{
        transform: translate(-30%,0);
      }
    }
  }
  ///////////////////////////////////////////////
  .nodes{ // the SVG
    position: relative;//**
    // bottom: 0;
    // background:rgba(red,0.3);
    height: 80%;
    // margin-top: 1.5em;
    min-width: 310px;//iphone 5
    max-width: 900px;
    width: 60%;
    overflow: visible;
    left: 50%;
    bottom: 0em;
    transform: translateX(-50%);
    // background: #6c96bb6e;
    .mobile &{
      position: absolute;//**
      bottom: 10em;
    }
    // @include respond-to('small'){
    //   height: 80%;
    //   // bottom: unset;
    //   bottom: 0em;
    // }
  
    svg{//iner svg emmbeded
      overflow: visible;
    }
    #Logo{
      font-size: 1em;
    }

    font-size: inherit;
  }
  

  svg{
    #Hq_location{
      .title{
        font-size:1.5em;
      }
    }
    #BackHome{
      transform: scale(.4) translate(2em, 2em);
    }
    line{
      stroke:$grays-level_1;
      &.dotted{
        @extend %dotted;
      }
    }
    .node{
      fill:$general-dark_blue;
      @extend %dotted;
      @include transition(all 300ms ease-in);
      &.clickable{
        stroke-dasharray: none;
        stroke: rgba($grays-level_3,0.8);
        stroke-width: 4px;
        // @include transition(all 300ms ease-in);
      }
      &#shadow{
        // stroke:3.3px;
        // transform: scale($shadow-scale);
        stroke-dasharray: none;
        stroke-width: 2em;
        transition-delay: 300ms;
        // @include transition(transform 200ms ease-in);
        // @include transition(all 200ms ease-in);
      }
      // &.shadow-off{
      //   stroke: transparent !important;
      // }
    }
    .active{
      .node{
        &.clickable{
          stroke: transparent;
          fill:$grays-level_1;
        }
      }
      #shadow{
        stroke: transparent !important;
      }
      >text{
        fill: $general-dark_blue;
      }
      circle, line{
        stroke-width: 0.5px;
        
      }
      .circle-item:last-child{
        circle{
          stroke-width: 1.1px;
        }
      }
    }

    .node-empty{
      fill:transparent;
      stroke: $grays-level_1;
      stroke-width: 1px;
    }
    text{
      fill:$grays-level_1;
      font-variant-caps: all-small-caps;
      &.sub-title{
        font-size: 0.81em;
        font-family: "GothamBook";
      }
    }

    .title{
      &.large{
        font-size:4em;
        font-weight:bold;
      }
    }
    .detail-out{
      pointer-events: none;
    }




    [class*="location-region"]{
      overflow: hidden;
      // fill: rgba(orange,0.3);
      touch-action: pan-y;
      .title{
        font-family: 'GothamMedium';
        text-transform: uppercase;
      }
      .item{
        font-family: 'GothamBook';
        font-variant: normal;
        font-size: 0.8em;
      }
      .scroll-bg{
        fill:$general-dark_blue;
        // fill:pink;
        // fill:transparent;
      }
      .scroll-btn{
        .scroll-btn-bg{
          fill:$general-dark_blue;
          // fill:rgba(black, 1);
        }
        text{fill:transparent}
        path{
          fill: transparent;
          stroke-width: 1px;
          stroke: $grays-level_1;
          @include transition(all 200ms ease-in );
        }
      }
      .line-region{
        stroke-width: 3px;
        stroke: $grays-level_1;
      }
      .line-country{
        stroke-width: 1.5px;
        stroke: $grays-level_1;
      }
    }
    #myDot{
      fill:$grays-level_3;
    }


    
   
    .companies-list{
      opacity:0;
      @include transition(opacity 300ms ease-in);
      .bg{
        opacity:0;
        fill:$general-dark_blue;
        @include transition(opacity 300ms ease-in);
        pointer-events: none;
      }
      line{
        stroke-dasharray: 500px;
        stroke-dashoffset: 500px;
        @include transition2(opacity 300ms ease-in, stroke-dashoffset 300ms ease-in);
      }
      [class*="company-item-"]{
          text{
            opacity:0;
            @include transition(opacity 300ms ease-in);
            font-family: 'GothamBook';
            text-transform: unset;
            font-variant-caps: unset;
            font-size:0.9em;
          }
          .date{
            fill: $grays-level_3;
          }
          line{ stroke: $grays-level_2; }
          circle{
            stroke: $grays-level_2;
            transform: translate(0, 0) scale(0) !important;
            @include transition(transform 300ms ease-in);
          }
          .bg{
            opacity: 0.8 !important;
          }
      }

      &.detail-show{
        display:block;
        opacity:1;
        .bg{
          opacity:1;
        }
        #shadow{
          stroke: transparent;
        }
        line{
          opacity:1;
          stroke-dashoffset: 0px !important;
        }
        [class*="company-item-"]{
             text{
               opacity:1;
             }
             circle{
               transform: translate(0, 0) scale(1) !important;
             }
        }

      }
    }





    .location-list{
      text{
        fill:$grays-level_1;
      }
      opacity: 0;
      display: none;
      @include transition(opacity 300ms ease-in);
      .bg{
        opacity:0;
        fill:$general-dark_blue;
        @include transition(opacity 300ms ease-in);
        pointer-events: none;
      }
      &.detail-out{
        opacity: 0;
        display: block;
      }
      &.detail-show{
        display:block;
        opacity: 1;
        .bg{
          opacity:1;
        }
      }

    }
   


    .logo{
      position:relative;
      width: 50px;
      circle{
        stroke-dasharray: none;
      }
      
    }
  }//svg
  ///////////////////////////////////////////////
  .name-copy{
    position:relative;
    width: 100%;
    bottom:0;
    left: 20%;
    padding-top:3em;
    .mobile &{
      position: absolute;
      padding-top:0em;
      height: 10em;
      // background: red;
      h1{
        
        // top:0;
      }
      .copy{
      
        // top: 4em;
      }
    }
    h1{
      position: relative;
      left: 0%;
      width: 80%;
      font-family: "Futura-Bold"; 
      font-size: 2em;
      line-height: 1em;
      margin: 0;
    }
    .copy{
      position: relative;
      left: 0%;
      width: 75%;
      color: $grays-level_1 !important;
    }

    @include respond-to('small') {
      left: 0%;
      h1{
        left: 50%;
        width: 64%;
        transform: translateX(-50%);
        line-height: 0.8em;
        text-align: center;
      }
      .copy{
        position: relative;
        left: 51%;
        width: 44%;
      }
    }
  }

  ///////////////////////////////////////////////
  .group-vert{
    position: relative;
    top:1em;
    // width: max-content;
    left:40%;
    
    // background: rgba(green,0.2);
    // margin: 0 auto;
    .group{
      // display: block;
      // position: relative;
      font-family:"GothamMedium";

      // font-size:0.7em;
      letter-spacing: 0.1em;
      display: inline-block;
        position: relative;
        // transform: translate(0%, -53%);
        font-size: 0.8em;
    }
    .vert{
      font-size:1em;
      font-family: "Futura-Medium";
      letter-spacing: 0.19em;
      // background: rgba(red,0.4);
      width: 58%;  
      display: inline-block;
      padding-left:0.4em;
      font-size: 1.1em;
      text-transform: uppercase;
    }

    @include respond-to('small') {
      left:50%;
      .group{
        // transform: translate(-110%, 53%);
        transform: translate(0, -0.2em);
      }
      .vert{
        // font-size:1.1em;
      }
    }

  }

  %hideGlobeOnMobile{
    display: block; //none
      @include respond-to('small'){
        display: block;
      }
  }
  ///////////////////////////////////////////////
  .close{
    @extend %button;
    position:absolute;
    box-sizing: border-box;
    margin: 1em 0 0 1em;
    // left: 1em;
    width:4em;
    height:4em;

    @include respond-to('small') {
      width:4.7em;
      height:4.7em;
      margin: 2em 0 0 2em;
      p{font-size: 0.8em;}
    }
    @include respond-to('medium') {
      width:6em;
      height:6em;
      margin: 3em 0 0 3em;
      p{font-size: 0.8em;}
    }
    @include respond-to('large') {
      width:7em;
      height:7em;
      margin: 4em 0 0 4em;
      p{font-size: 0.8em;}
    }
    &::after{
      content:'';
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba($grays-level_3,0.3);
      @include box-shadow(0px 3px 29px 5px rgba($grays-level_3,.3));
      top:0;
      left:0;
      @include border-radius(100%);
      @extend %hideGlobeOnMobile;
    }
    &:hover{
      &::after{
        // background-color: rgba($grays-level_3,0.5);
        @include box-shadow(0px 3px 29px 5px rgba($grays-level_3,.5));
      }
      p{
        color:$grays-level_1;
      }
    }
    @include transition(all 300ms ease-in);
    img{
      width:100%;
      position: absolute;
      top:0;
      left:0;
      @extend %hideGlobeOnMobile;
    }
    P{
      margin: 0;
      padding:0;
      top:0;
      left:0;
      font-size: 0.7em;
      position: absolute;
      top:50%;
      width: 100%;
      transform: translateY(-0.8em);
      color:$grays-level_1;
      text-transform: uppercase;
      font-variant-caps: all-small-caps;
      @include transition(color 300ms ease-in);
    }
    @include transition(opacity 300ms ease-in);
  }
  [class*="arrow"]{
    @extend %button;
    position: absolute;
    box-sizing: border-box;
    bottom: 5%;
    width: 3em;
    // height: 1em;
    @include respond-to('small') {
      bottom: 20%;
    }
    .mobile &{
      bottom: 20%;
      transform: scale(0.6);
    }
    svg{
      width: 1.3em;
      polyline{
        stroke-width: 3px;
        stroke: $grays-level_2;
      }
    }
    @include transition(opacity 300ms ease-in);
  }
  .arrow-next{
    right: 8%;
    .mobile &{
      right: 2%;
    }
   
  }
  .arrow-previous{
    left: 8%;
    .mobile &{
      left: 2%;
    }
  }
 &.detail-active{
  [class*="arrow"], .close{
    opacity: 0;
    pointer-events: none;
  }
  #Add_on_companies, #Other_locations{
    // pointer-events: none;
  }
  
  .center{
    &:last-child{
      pointer-events: auto !important;
    }
  }
 }
 &.training-mode{
  #Add_on_companies .shadow{
    animation-delay:80ms;
  }
  .shadow{
    @include animation(ping 2s ease-out 1  both);
    opacity: 0;
    -webkit-transform: scale( 0.95 );
    transform: scale( .95 );
  }
 }
}

