@keyframes ping {
  0%{
    opacity: 0;
    -webkit-transform: scale( 0.95 );
    transform: scale( .95 );    
  }
  10% {
    opacity: 0.8;
  }
  40% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    opacity: 0;
  }
  50% {
    -webkit-transform: scale(2.2);
            transform: scale(2.2);
    opacity: 0;
  }
  51%{
    opacity: 0;
    -webkit-transform: scale( 0.95 );
    transform: scale( .95 );    
  }
  60% {
    opacity: 0.8;
  }
  85% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    opacity: 0;
  }
  92% {
    -webkit-transform: scale(2.2);
            transform: scale(2.2);
    opacity: 0;
  }
  95% {
    -webkit-transform: scale( 0.95 );
            transform: scale( 0.95 );
    opacity: 0;
  }
  100% {
    -webkit-transform: scale($shadow-scale);
            transform: scale($shadow-scale);
    opacity: 1;
  }
}

