.full-frame{
  position:absolute;
  box-sizing: border-box;
  margin:0;
  width:100%;
  height:100%;
}
$element-layers: (
  globe: auto,
  list: 2,
  filter: 3,
  zoom-bar: 5,
  company: 10,
  instructions: 15,
  disclosure: 20,
);
$fade-timeout: (
  appear: 500ms,
  enter: 500ms,
  exit: 500ms,
);

$globe-list:(
  'globe': 70%,
  'list': 35%,
  // 'globe-height': 70%
);

.hide{
  display:none;
}

%button{
  background:transparent;
  border:none;
  &:focus{
    outline: none;
  }
}