@import '../../styles/components.scss';
@import "../../styles/mixins.scss";
@import "../../styles/colors.scss";
@import "../../styles/typography.scss";

$fade-timeout: (
  appear: 500ms,
  enter: 500ms,
  exit: 500ms,
);

#disclosure{
  box-sizing:border-box;
  margin:0;
  top: 0;
  left: 0;
  z-index: map-get($element-layers, disclosure);
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  
  .copy{
    pointer-events: auto;
    position: absolute;
    box-sizing:border-box;
    width:100%;
    height:100%;
    background: $general-dark_blue;
    color: $grays-level_1;
    &.disable{
      pointer-events: none;
    }
    line-height: 1.8em;
    font-family:"GothamBook";
    font-size: 10px;
    @include respond-to('small') {
      font-size: 10px;
    }
    @include respond-to('medium') {
      font-size: 14px;
    }
    @include respond-to('large') {
      font-size: 14px;
    }
    
    h3{
      font-family:"GothamMedium";
      text-transform: uppercase;
      letter-spacing: 0.2em;
      font-size: 10px;
      @include respond-to('small') {
        font-size: 10px;
      }
      @include respond-to('medium') {
        font-size: 14px;
      }
      @include respond-to('large') {
        font-size: 14px;
      }
    }
    
    .center{
      position: relative;
      box-sizing:border-box;
      width:70%;
      max-width: 600px;
      margin: 0 auto;
      height:100%;
    }

    .close{
      position:absolute;
      top:1em;
      right:1em;
      transform:translate(-100%, 100%);
      font-size:2em;
      @extend %button;
      @include respond-to('small', portrait) {
        font-size:1.4em;
      }
      @include respond-to('medium', portrait) {
        font-size:1.6em;
      }
      @include respond-to('large', portrait) {
        font-size:2em;
      }
      &:hover{  
        &::after, &::before{
          background: $grays-level_1;
        }
      }
      &::after, &::before{
        content:'';
        position:absolute;
        width:1em;
        height:0.16em;
        box-sizing: border-box;
        border: none;
        background: $grays-level_2;
        font-size:inherit;
      }
      &::before{
        transform: rotate(45deg);        
        @include transition2(transform 200ms ease-in, background 200ms ease-in);
      }
      &::after{
        transform: rotate(-45deg);        
        @include transition2(transform 200ms ease-in, background 200ms ease-in);
      }
      .hit-area{
        position: absolute;
        width: 3em;
        height: 3em;
        transform: translate(-35%, -40%);
        
      }
    }

  }


  .open-button{
    @extend %button;
    pointer-events: auto;
    position: absolute;
    box-sizing:border-box;
    bottom: 2em;
    right: 1em;
    color:$grays-level_3;
    @include transition(color 200ms ease-in);
    font-family:"GothamMedium";
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 10px;
    @include respond-to('small') {
      font-size: 10px;
    }
    @include respond-to('medium') {
      font-size: 14px;
    }
    @include respond-to('large') {
      font-size: 14px;
    }
    &::after, &::before{
      content:'';
      position:absolute;
      width:2em;
      height:2em;
      @include border-radius(100%);
      top: -2.2em;
      left: 50%;
      box-sizing: border-box;
    }
    &::before{
      border: 2px solid $grays-level_3;
      background:transparent;
      transform: translateX(-50%);
      @include transition(border 200ms ease-in);
    }
    &::after{
      border: none;
      background:$grays-level_3;
      transform: translateX(-50%) scale(0.6);
      @include transition(background 200ms ease-in);
    }
    &:hover{
      color:$grays-level_1;
      &::before{
        border: 2px solid $grays-level_1;
      }
      &::after{
        background:$grays-level_1;
      }
    }
  }

