@import '../../../styles/components.scss';
@import "../../../styles/mixins.scss";
@import "../../../styles/colors.scss";
@import "../../../styles/typography.scss";

#zoom-bar{
  position: absolute;
  height: 1em;
  width: 80%;
  background: transparent;
  left:10%;
  bottom: 10%;
  z-index: map-get($element-layers, zoom-bar);
  @include orientation(portrait){
    bottom: 2%;
    // bottom: 10%;
    width:70%;
    left:15%;
  }
  @include respond-to('small', portrait) {
    bottom: 5%;
  }
  &::before{
    content:'';
    position:absolute;
    width:100%;
    height: 1px;
    top:50%;
    left:0;
    background: $grays-level_2;
    transform:translateY(-50%);
  }
  button{
    @extend %button;
    position:relative;
    height:100%;
    color: $grays-level_1;
    font-weight:bold;
    padding:0 .8em;
    line-height:0;
    font-size:2em;
    &.plus{
      float:right;
      transform:translate(100%,-0.06em);
    }
    &.minus{
      float:left;
      transform:translate(-100%,-0.06em);
    }
  }
  .bar{
    background: $grays-level_1;
    position:absolute;
    height:100%;
    @include transition(width 300ms ease-in-out);
  }
}
